.playerAccess{
    width: 80%;
    position: absolute;
    max-width: 600px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    padding: 1.5em;
    background: rgba(255,255,255,1);
}

.dark-playerAccess{
    /*border: 025px solid rgba(50, 50, 50, 0.9);*/
    background: rgba(50,50,50,1);
    
}

.playerAccess h2{
    background-image: linear-gradient(to right bottom, red,orange,yellow);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
}

.card-body *{
    width: 80%;
}

@media only screen and (max-width: 576px) {
    /* width */
    .card-body *{
        width: 100%;
    }
}